import React from "react"; 
import Cards from "./Cards";
import web from "./coffee.jpg";
import Sdata from "./Sdata"; 

const Service = () => {
	return (
<div>
				<div className='my-5'>
					<h1 className='text-center'>Our services</h1>
                    <h5 style={{opacity:0.7}} id="des"> We provide direct download of files IN ONE CLICK without letting you to go on any other malicious websites **Stay Tuned for more Courses**</h5>
				</div>

          <div className="container-fluid mb-5">
           			 <div className="row">
            				<div className='col-10 mx-auto'>
            					<div className='row gy-1'>
            						{
            							Sdata.map((val, ind) =>{
            								return <Cards key={ind}
            								imgsrc={val.imgsrc}
            								title={val.title}
                                            text={val.text}
                                            button={val.button}
                                            />
            							})
            						}
            					</div>
            				</div>	 
            		 </div>

		  </div>
</div> 
		);
};

export default Service;